.funnel-bar {
  position: relative;
  height: 30px;
  line-height: 30px;
}
.funnel-bar-left {
  text-align: left;
}
.funnel-bar-center {
  text-align: center;
}
.funnel-bar-right {
  text-align: right;
}
.funnel-bar .funnel-bar-value {
  display: inline-block;
  vertical-align: top;
  height: 30px;
}
.funnel-bar .funnel-bar-label {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  position: absolute;
  left: 0;
  right: 0;
}
