.drag-handle {
  vertical-align: bottom;
  cursor: move;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: stretch;
      -ms-flex-align: stretch;
          align-items: stretch;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.drag-handle:before {
  content: '';
  display: block;
  width: 6px;
  background: -o-linear-gradient(left, transparent 0px, white 1px, white 2px) center, -o-linear-gradient(transparent 0px, white 1px, white 2px) center, #111111;
  background: linear-gradient(90deg, transparent 0px, white 1px, white 2px) center, linear-gradient(transparent 0px, white 1px, white 2px) center, #111111;
  background-size: 2px 2px;
}
.sortable-container {
  -webkit-transition: background-color 200ms ease-out;
  -o-transition: background-color 200ms ease-out;
  transition: background-color 200ms ease-out;
  -webkit-transition-delay: 300ms;
       -o-transition-delay: 300ms;
          transition-delay: 300ms;
}
.sortable-container.sortable-container-dragging {
  -webkit-transition-delay: 0s;
       -o-transition-delay: 0s;
          transition-delay: 0s;
  background-color: #f6f8f9;
}
