.color-swatch {
  display: inline-block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  vertical-align: middle;
  border-radius: 2px;
  overflow: hidden;
  width: 12px;
  background-color: transparent;
  background-image: -o-linear-gradient(45deg, rgba(0, 0, 0, 0.1) 25%, transparent 25%), -o-linear-gradient(135deg, rgba(0, 0, 0, 0.1) 25%, transparent 25%), -o-linear-gradient(45deg, transparent 75%, rgba(0, 0, 0, 0.1) 75%), -o-linear-gradient(135deg, transparent 75%, rgba(0, 0, 0, 0.1) 75%);
  background-image: linear-gradient(45deg, rgba(0, 0, 0, 0.1) 25%, transparent 25%), linear-gradient(-45deg, rgba(0, 0, 0, 0.1) 25%, transparent 25%), linear-gradient(45deg, transparent 75%, rgba(0, 0, 0, 0.1) 75%), linear-gradient(-45deg, transparent 75%, rgba(0, 0, 0, 0.1) 75%);
  background-size: 12px 12px;
  background-position: 0 0, 0 6px, 6px -6px, -6px 0px;
}
.color-swatch:before {
  content: "";
  display: block;
  padding-top: calc(100% - 2px);
  background-color: inherit;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  overflow: hidden;
}
